	// 监听页面返回事件
	onBackPress() {
		// 关闭模态框 
		for(let key in this.popup) {
			if(this.popup[key] !== 'none'){
				this.hide(key)
				return true
			}
		}
	},
	methods: {
		...mapMutations([
			'addGoodsToCart'
		]),
		// 加入购物车
		addCart(){
			// 组织数据
			let goods = this.detail
			goods['checked'] = false
			goods['attrs'] = this.selects
			// 加入购物车
			this.addGoodsToCart(goods)
			// 隐藏筛选框
			this.hide('attr')
			// 成功提示
			uni.showToast({
				title: '加入成功'
			});
		},
		hide(key){
			this.popup[key] = 'show'
			setTimeout(()=>{
				this.popup[key] = 'none'
			}, 200);
		},
